<template><section><html><head></head><body><h1>Primitive</h1>
<p>The <code v-pre="">vc-primitive</code> component is used to load a primitive that represents geometry in the Scene.</p>
<h2>Example</h2>
<h3>Load Primitive with <code v-pre="">vc-primitive</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive @click="clicked" :appearance="appearance" :geometry-instances="geometryInstances"></vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
