<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>geometryInstances</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre="">optional</code> The geometry instances - or a single geometry instance - to render.</td>
</tr>
<tr>
<td>appearance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The appearance used to render the primitive.</td>
</tr>
<tr>
<td>depthFailAppearance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The appearance used to shade this primitive when it fails the depth test.</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if this primitive will be shown.</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The 4x4 transformation matrix that transforms the primitive (all geometry instances) from model to world coordinates.</td>
</tr>
<tr>
<td>vertexCacheOptimize</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> When true, geometry vertices are optimized for the pre and post-vertex-shader caches.</td>
</tr>
<tr>
<td>interleave</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> When true, geometry vertex attributes are interleaved, which can slightly improve rendering performance but increases load time.</td>
</tr>
<tr>
<td>compressVertices</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> When true, the geometry vertices are compressed, which will save memory.</td>
</tr>
<tr>
<td>releaseGeometryInstances</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> When true, the primitive does not keep a reference to the input geometryInstances to save memory.</td>
</tr>
<tr>
<td>allowPicking</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> When true, each geometry instance will only be pickable with Scene#pick. When false, GPU memory is saved.</td>
</tr>
<tr>
<td>cull</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> When true, the renderer frustum culls and horizon culls the primitive's commands based on their bounding volume. Set this to false for a small performance gain if you are manually culling the primitive.</td>
</tr>
<tr>
<td>asynchronous</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if the primitive will be created asynchronously or block until ready.</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> For debugging only. Determines if this primitive's commands' bounding spheres are shown.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Determines whether this primitive casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
</tbody>
</table>
<hr>
<p>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Primitive.html">Primitive</a></strong></p>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse is pressed on this primitive.</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse bounces on the primitive.</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse clicks on the primitive.</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Touch when the mouse clicks outside the primitive.</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the left mouse button double-clicks the primitive.</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves on this primitive.</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves to this primitive.</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves out of the primitive.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometryInstances: null,
          geometry: null,
          polygonHierarchy: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 108.2, lat: 35.5 },
            { lng: 102.1, lat: 33.5 }
          ],
          height: 200,
          rectangle: { west: 100.5, south: 29.5, east: 115.5, north: 35.5 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance

          this.geometryInstances = new Cesium.GeometryInstance({
            geometry: new Cesium.RectangleGeometry({
              rectangle: Cesium.Rectangle.fromDegrees(
                this.rectangle.west,
                this.rectangle.south,
                this.rectangle.east,
                this.rectangle.north
              )
            })
          })
          this.appearance = new Cesium.MaterialAppearance({
            material: Cesium.Material.fromType('Checkerboard', {
              repeat: new Cesium.Cartesian2(20.0, 6.0)
            }),
            materialSupport: Cesium.MaterialAppearance.MaterialSupport.TEXTURED
          })
        },
        clicked(e) {
          console.log(e)
        }
      }
    }
  </script>